.wrapper__seniorWorkerReports {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    justify-items: center;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-name: show;
            animation-name: show;
}

.wrapper__seniorWorkerReports__list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    list-style: none;
    padding: 0;
    margin: 0;
    text-align: center;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-name: show;
            animation-name: show;
}

@-webkit-keyframes show {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes show {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.wrapper__seniorWorkerReports__list_hidden {
    display: none;
}

.wrapper__seniorWorkerReports__list_toggle {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    cursor: pointer;
}

.wrapper__seniorWorkerReports__listTicketsItems_inactive {
    color: #000;
}

.wrapper__seniorWorkerReports__listTicketsItems_active {
    color: #3072c4;
}

.wrapper__seniorWorkerReports__list_arrowUp {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-name: openRotate;
            animation-name: openRotate;
}


.wrapper__seniorWorkerReports__list_arrowDown {
    /*transition: all 1s;*/
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-filter: invert(0.5) sepia(1) hue-rotate(170deg) saturate(4) brightness(0.7);
            filter: invert(0.5) sepia(1) hue-rotate(170deg) saturate(4) brightness(0.7);
        -webkit-animation-name: closedRotate;
                animation-name: closedRotate;
}

@-webkit-keyframes openRotate {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
}

@keyframes openRotate {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
} 

@-webkit-keyframes closedRotate {
    from {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
    to {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
} 

@keyframes closedRotate {
    from {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
    to {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

.wrapper__seniorWorkerReports__list_links {
    color: #3072C4;
    margin: 20px 0;
}

.wrapper__seniorWorkerReports__list_links:hover {
    text-decoration: none;
}